@import url(https://fonts.googleapis.com/css?family=Barlow:400,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barlow:400,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barlow:400,600&display=swap);
html{
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#projectsHeaderImg {
  background-image: url(../../static/media/projectsHeader.f4c56643.png);
  background-repeat: no-repeat;
  background-size: contain;
  opacity: 0.9;
}

h1,
h2,
h3,
figcaption,
p,
a,
span {
  font-family: "Barlow", sans-serif;
  color: #1c307a;
}

.description {
  font-size: 20px;
  text-align: left;
  padding-top: 20%;
  line-height: 40px;
}

h1 {
  text-align: left;
  font-style: normal;
  font-size: 36px;
  padding-bottom: 7%;
}

h2 {
  text-align: left;
  font-style: normal;
  font-size: 30px;
}

p {
  text-align: left;
}

#bluebg {
  background: #eef1f8;
}

section {
  text-align: center;
  padding: 10%;
}

#pgHeading {
  padding: 18%;
  padding-top: 22.5%;
}

.w-100 {
  padding-top: 5%;
}

figure {
  display: inline-block;
}

figure img {
  vertical-align: top;
}

figcaption {
  font-size: 18px;
  text-align: center;
}

.boldHeader,
.figure-caption {
  font-weight: 600;
}

.card {
  padding: 1%;
  height: 499px;
}

.card-body {
  padding: 10%;
}

.carousel .carousel-indicators li {
  background-color: grey;
  border-radius: 100%;
  height: 20px;
  width: 20px;
}

#projectsCarousel {
  padding-bottom: 8%;
}

#aboutUsHeaderImg {
  background-image: url(../../static/media/aboutHeader.b5c09919.png);
  background-repeat: no-repeat;
  background-size: contain;
  opacity: 0.9;
}

.position {
  font-size: 14px;
}

.col {
  padding: 5%;
}

.pageTitle {
  font-weight: bold;
  font-size: 15vw;
  line-height: 240px;
  color: #1c307a;
  -webkit-transform: rotate(-11.5deg);
          transform: rotate(-11.5deg);
}

.titleDiv {
  padding-top: 4%;
  text-align: center;
}

.boldHeader,
.figure-caption {
  font-weight: 600;
}

.contactDescription {
  padding-top: 10%;
  font-size: 30px;
  line-height: 60px;
}

.navbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #ABC5ED;
    height: 137px;
}

.navbar-navigator{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    flex: 1 1;
}

.navbar-logo{
    margin-left: 0.5rem;
}

.navbar-logo a{
    padding: 0 3rem;
}

.navbar-items-spacer{
    flex: 1 1;
}

.navbar-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.navbar-items li{
    padding: 0 3rem;
}

.navbar-items a{
    color: #1C307A;
    font-family: "Barlow", sans-serif;
    font-size: 24px;
    font-weight: bold;
    text-decoration: none;
}

.navbar-items a:hover,
.navbar-items a:active{
    color: #f9aa00;
    text-decoration: none;
}

@media (max-width: 768px){
    .navbar-items{
        display: none;
    }
}

@media (min-width: 769px){
    .sidebar-toggle-button{
        display: none;
    }

    .navbar-logo{
        margin-left: 0;
    }
}
.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 30px;
    width: 36px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus{
    outline: none;
}

.toggle-button-line{
    width: 36px;
    height: 2px;
    background: white;
}
.footer{
    position: relative;
    width: 100%;
    background: #ABC5ED;
    height: 124px;
}

.footer-links{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.footer-items ul{
    list-style: none;
    margin: 0;
    padding: 0 15rem;
    display: flex;
}

.footer-items li{
    padding: 0 0.5rem;
}
.sidebar{
    height: 100%;
    background: white;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out
}

.sidebar.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.sidebar ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sidebar li{
    margin: 0.5rem 0;
}

.sidebar a{
    color: #1C307A;
    font-family: "Barlow", sans-serif;
    font-size: 24px;
    font-weight: bold;
    text-decoration: none;
}

.sidebar a:hover,
.sidebar a:active{
    color: #f9aa00;
    text-decoration: none;
}

@media (min-width: 769px){
    .sidebar{
        display: none;
    }
}
.pagefade{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
